import axios from "axios";
const state = {
  UserProfile: {},
  Files: [],
  FilesManage: [],
};

const getters = {
  UserProfile: (state) => state.UserProfile,
  Files: (state) => state.Files,
  FilesManage: (state) => state.FilesManage,
};

const mutations = {
  SET_USER_PROFILE(state, data) {
    state.UserProfile = data;
  },
  SET_FILES(state, data) {
    state.Files = data;
  },
  SET_FILES_MANAGE(state, data) {
    state.FilesManage = data;
  },
};

const actions = {
  getUserProfile({ commit }) {
    axios
      .get("/user/profile")
      .then((res) => {
        commit("SET_USER_PROFILE", res.data.user);
      })
      .catch((err) => {
        console.log(err);
      });
  },
  getFiles(
    { commit },
    {
      StartDate = "",
      EndDate = "",
      customerName = "",
      victimName = "",
      sinisterNumber = "",
    }
  ) {
    return new Promise((resolve, reject) => {
      axios
        .get(
          `/indermnity?startDate=${StartDate}&endDate=${EndDate}&customerName=${customerName}&victimName=${victimName}&sinisterNumber=${sinisterNumber}`
        )
        .then((res) => {
          // alert(JSON.stringify(res))
          commit("SET_FILES", res.data.data);
          resolve(res.data.data);
        })
        .catch((err) => {
          commit("SET_FILES", {});
          reject(err);
          console.log(err);
        });
    });
  },
  getDownloadManageRecap({ commit }, { id = "" }) {
    // alert(JSON.stringify(id));
    return new Promise((resolve, reject) => {
      axios
        .get(`/indermnity/${id}/print-manager-recap-expertise`)
        .then((res) => {
          window.open(res.data.data, "_blank");
          commit("SET_FILES_MANAGE", res.data.data);
          resolve(res.data.data);
        })
        .catch((err) => {
          commit("SET_FILES_MANAGE", {});
          reject(err);
          console.log(err);
        });
    });
  },
  getDownloadMedicalExpertiseReport({ commit }, { id = "" }) {
    // alert(JSON.stringify(id));
    return new Promise((resolve, reject) => {
      axios
        .get(`/indermnity/${id}/print-medical-expertise-report`)
        .then((res) => {
          window.open(res.data.data, "_blank");
          commit("SET_FILES_MANAGE", res.data.data);
          resolve(res.data.data);
        })
        .catch((err) => {
          commit("SET_FILES_MANAGE", {});
          reject(err);
          console.log(err);
        });
    });
  },
  getDownloadMedicalExpertiseRequest({ commit }, { id = "" }) {
    // alert(JSON.stringify(id));
    return new Promise((resolve, reject) => {
      axios
        .get(`/indermnity/${id}/print-medical-expertise-request`)
        .then((res) => {
          window.open(res.data.data, "_blank");
          commit("SET_FILES_MANAGE", res.data.data);
          resolve(res.data.data);
        })
        .catch((err) => {
          commit("SET_FILES_MANAGE", {});
          reject(err);
          console.log(err);
        });
    });
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
